.france {
  /* OPTIONS ====== */
  /* BASE ========= */
  width: 600px;
  height: 633px;
  font-size: 15px;
  margin: 3em auto;
  font-family: "Open Sans Condensed", sans-serif;
  font-weight: 700;
  text-transform: uppercase;
  /* THE MAGIC ==== */
  position: relative;
  overflow: hidden;
}
.france a {
  color: grey;
  text-decoration: none;
  transition: all 0.15s ease-in-out;
}
.france a.auve, .france a.cors, .france a.lorr, .france a.npdc, .france a.poit {
  color: rgba(9, 96, 153, 0.2);
}
.france a.alsa, .france a.aqui, .france a.pica, .france a.cent, .france a.bret {
  color: rgba(9, 96, 153, 0.4);
}
 .france a.rhon {
  color: #e74c3c;
}

.france a.idfr {
  color: #d57220;
}

.france a.bour, .france a.hnor, .france a.pdll, .france a.mipy {
  color: rgba(9, 96, 153, 0.6);
}
.france a.cham, .france a.lang, .france a.limo {
  color: rgba(9, 96, 153, 0.8);
}
.france a.bnor, .france a.fran, .france a.paca {
  color: #096099;
}
/* .france a:hover, .france a:focus {
  color: #e74c3c;
} */
.france span {
  position: absolute;
  z-index: 0;
  line-height: 70%;
}
.france a:hover span, .france a:focus span {
  z-index: 1;
}
.france .visuallyhidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}
